export const introText = () => {
  return (
    <span className={`m-auto text-white h-2/3 mr-28 ml-28`}>
      This entire website was built using JS, React Three Fiber, and the React
      framework. React Three Cannon is also implemented for physics simulation,
      so feel free to drag around any of the 3D Objects you see!
    </span>
  );
};
export const DSText = () => {
  return (
    <span className={`m-auto text-white h-2/3 mr-28 ml-28`}>
      Working with the Digital Scholarship Lab at the University of Rochester
      was when I was first introduced to JS and React. Refactoring functionality
      in Resurrect3D, a Digital Heritage 3D model viewer, was where I was first
      introduced to React Three Fiber. I also got hands-on experience with basic
      3D modeling in Blender. Most of the models on this website are made by me!
    </span>
  );
};
export const XRText = () => {
  return (
     <div className="h-2/3 mr-28 ml-28">
    <span className={`m-auto text-white `}>
      StudioX is an XR peer-to-peer learning space for VR, AR, and MR
      experiences. As a Karp Library Fellow, I serve as StudioX's XR Developer.
      I curate workshopes for students, faculty, and community members on
      developing XR applications using Unity for a variety of devices. Engaging
      with other peers, I've developed and assisted in the production of AR and
      VR research-related prototypes. I've also presented at various virtual and
      in-person events on the topic of XR in education.
    </span>
    </div>
  );
};
